import React, { useContext, useEffect, useState } from 'react';
import { Button, Col, Dropdown, Form, Modal, Row } from 'react-bootstrap';
import { Context } from '../../index';
import { getVendors } from '../../utils/VendorApi';
import { observer } from 'mobx-react-lite';
import { createProduct } from '../../utils/ProductApi';

const CreateProduct = observer(({ show, onHide }) => {

  const { product } = useContext(Context);
  
  const [name, setName] = useState('');
  const [price, setPrice] = useState(0);
  const [file, setFile] = useState(null);
  const [info, setInfo] = useState('');

  const selectFile = (e) => {
    setFile(e.target.files[0]);
  }

  const addProduct = () => {
    console.log(name, price, file, product.selectedVendor.id, info)
    const formData = new FormData();
    formData.append('name', name);
    formData.append('price', `${price}`);
    formData.append('img', file);
    formData.append('vendorId', product.selectedVendor);
    formData.append('info', info);
    createProduct(formData).then(data => onHide())
    //createProduct({name, price, vendorId: product.selectedVendor.id}).then(data => onHide())
  }

  console.log(product)

  useEffect(() => {
    getVendors().then(data => product.setVendors(data))
  }, [])

  return (
    <Modal
      show={show}
      onHide={onHide}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
           
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Form>
          <Dropdown className='mt-2 mb-2'>
            <Dropdown.Toggle>{product.selectedVendor.name || ' '}</Dropdown.Toggle>
            <Dropdown.Menu>
              {Object.keys(product.vendors).map(vendor =>
                <Dropdown.Item onClick={() => product.setSelectedVendor(vendor)} key={vendor}>{product.vendors[vendor]}</Dropdown.Item>  
              )}
            </Dropdown.Menu>
            <Form.Control 
              className='mt-3'
              value={name}
              onChange={e => setName(e.target.value)}
              placeholder='  '
            />
            <Form.Control 
              className='mt-3'
              value={price || '  '}
              onChange={e => setPrice(Number(e.target.value))}
              placeholder='  '
              type='number'
            />
            <Form.Control 
              className='mt-3'
              value={info}
              onChange={e => setInfo(e.target.value)}
              placeholder='  '
            />
            <Form.Control 
              className='mt-3'
              type='file'
              onChange={selectFile}
            />
          </Dropdown>
        </Form>
      </Modal.Body>
      <Modal.Footer>
        <Button variant='outline-danger' onClick={onHide}></Button>
        <Button variant='outline-success' onClick={addProduct}></Button>
      </Modal.Footer>
    </Modal>
  );
});

export default CreateProduct;